.news-photo {
  margin-bottom: 80px;
  &__slider {}

  &__slide {
    img {
      display: block;
      border: 2px solid var(--Blue, #1078d3);
      border-radius: 20px;
      width: 100%;
      height: 500px;

      @include mobile {
        height: 400px;
      }
    }
  }

  @include desktop {
    margin-bottom: 50px;
  }

  @include mobile {
    margin-bottom: 30px;
  }
}
