.map {
  position: relative;

  #map {
    height: 650px;

    .ymaps-2-1-79-map {
      height: 650px !important;

      @include tablet {
        height: 600px !important;
      }

      @include mobile {
        height: 400px !important;
      }
    }

    @include tablet {
      height: 600px;
    }

    @include mobile {
      height: 400px !important;
    }
  }
}
