.card-text {
  margin-bottom: 80px;

  &__wrapper {
    border: 2px solid var(--Blue, #1078d3);
    border-radius: 10px;
    padding: 20px;

    @include mobile {
      padding: 10px;
    }
  }

  &__text {
    margin: 0;
    margin-bottom: 20px;
    font-weight: 400;
    font-size: 24px;
    line-height: 130%; /* 31.2px */
    letter-spacing: 0.72px;
    color: var(--txt-dark-blue, #031367);

    @include mobile {
      font-size: 15px;
    }
  }

  @include desktop {
    margin-bottom: 50px;
  }

  @include mobile {
    margin-bottom: 30px;
  }
}
