.search {
  position: relative;
}

.search input[type="text"] {
  border: none;
  border-radius: 10px;
  padding: 10px 12px;
  width: 100%;

  &::placeholder {
    font-family: var(--second-family, sans-serif);
    font-weight: 400;
    font-style: normal;
    font-size: 12px;
    line-height: 102%; /* 12.24px */
    letter-spacing: 0.36px;
    color: var(--txt-blue, #107bd7);
  }
}

.search .results {
  position: absolute;
  left: 0;
  top: 38px;
  z-index: 10;
  list-style-type: none;
  overflow-y: auto;
  margin: 0;
  border: 2px solid var(--Blue, #1078d3);
  border-radius: 10px;
  padding: 0;
  width: 100%;
  max-height: 200px;
  background-color: #fff;
}

.search .results li {
  padding: 10px;
  cursor: pointer;
}

.search .results li:hover {
  background-color: #f2f2f2;
}
