.card-cert {
  margin-bottom: 80px;

  &__slider {
    .hero-slider__btn {
      color: #1078d3;
      background-color: #031367;
      opacity: 0.6;
    }
  }

  &__slide {
    border: 2px solid var(--Blue, #1078d3);
    border-radius: 10px;
    max-width: 400px;

    img {
      display: block;
      width: 100%;
    }

    @include mobile {
      margin: 0 20px;
    }
  }

  @include desktop {
    margin-bottom: 50px;
  }

  @include mobile {
    margin-bottom: 30px;
  }
}
